						<a :href="item.mainlink">{{item.maintxt}}</a>
						<ul class="depth2">
							<li v-for="(subitem, subindex) in item.subdata" :key="subindex">
								<a :href="subindex.sublink">{{subitem.subtxt}}</a>
							</li>
						</ul>
					</li>
					<!-- <li>
						<a href="#">ÌöåÏÇ¨ÏÜåÍ∞ú</a>
						<ul class="depth2">
							<li><a href="#">Ïù∏ÏÇ¨Îßê</a></li>
							<li><a href="#">STXÍ±¥ÏÑ§</a></li>
							<li><a href="#">Í∏∞ÏóÖÎ¨∏Ìôî</a></li>
							<li><a href="#">STXÍ±¥ÏÑ§Ïó∞ÌòÅ</a></li>
							<li><a href="#">Ï°∞ÏßÅÏïàÎÇ¥</a></li>
							<li><a href="#">Ïú§Î¶¨Í≤ΩÏòÅ</a></li>
							<li><a href="#">ÏïàÏ†Ñ¬∑ÌôòÍ≤Ω¬∑ÌíàÏßàÍ≤ΩÏòÅ</a></li>
							<li><a href="#">Ï∞æÏïÑÏò§ÏãúÎäîÍ∏∏</a></li>
						</ul>
					</li> -->
				</ul>
			</nav>
			<div class="lang">
				<a href="#">ENG</a>
				<i class="lang-bar"></i>
				<a href="#" class="lang-active">KOR</a>
			</div>
		</div>
	</header>
</template>

<script>
	import $ from 'jquery';
	import { onUpdated, computed } from 'vue'; // jquery Ïì∞Í∏∞ÏúÑÌï¥ÏÇ¨Ïö©
	import { useStore } from 'vuex';

	export default {
		setup() {
			const store = useStore();
			const gnbData = computed ( () => store.getters.getGnbData);
			// 
			store.dispatch('fetchGnbdata');

			onUpdated(() => {
				let header = $('.header');
				let gnb = $('.gnb');
				// ÌéºÏ≥êÏ°åÏùÑ ÎïåÏùò ÎÜíÏù¥ Í∞í
				let gnbMaxHeight = gnb.outerHeight();
				// Îã´ÌòîÏùÑ ÎïåÏùò ÎÜíÏù¥ Í∞í
				let gnbMinHeight = header.outerHeight();
				header.css('height', gnbMinHeight);

				gnb.mouseenter(function () {
					header.css('height', gnbMaxHeight);
				});

				gnb.mouseleave(function () {
					header.css('height', gnbMinHeight);
				});
				// Ï£ºÎ©îÎâ¥ Ìè¨Ïª§Ïä§ Í∏∞Îä•

				let depth_1_Li = $('.depth1 > li');
				$.each(depth_1_Li, function () {
					$(this).mouseenter(function () {
						// li Ïùò > a Î•º Ï∞æÎäîÎã§.
						$(this).find('>a').addClass('depth1-focus');
					});
					$(this).mouseleave(function () {
						// li Ïùò > a Î•º Ï∞æÎäîÎã§.
						$(this).find('>a').removeClass('depth1-focus');
					});
				});
			});
			return {
				gnbData
			}
		}

	}
</script>

<style>
	.header {
		position: fixed;
		left: 0;
		top: 0;
		overflow: hidden;
		width: 100%;
		height: 80px;
		transition: height 0.5s;
		z-index: 9999;
		box-shadow: 0px 0px 7px 0px rgb(0 0 0 / 20%);
	}

	.header-open {
		height: 400px;
	}

	.header::before {
		content: '';
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		top: 0;
		width: 100vw;
		height: 80px;
		background: #fff;
		box-shadow: 0px 0px 7px 0px rgb(0 0 0 / 20%);
	}

	.header .inner {}

	.logo {
		position: absolute;
		right: calc(100% + 100px);
		top: 20px;
		width: 150px;
		height: 39px;
		background: url('../assets/images/logo.png') no-repeat center;
		background-size: cover;
		font-size: 0;
	}

	.gnb {
		position: relative;
	}

	.gnb::before {
		content: '';
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		top: 80px;
		display: block;
		width: 100vw;
		height: calc(100% - 80px);
		background-color: rgba(0, 0, 0, 0.5);
	}

	.depth1 {
		position: relative;
		display: table;
		table-layout: fixed;
		width: calc(180px * 6 - 70px);
		margin: 0 auto;
	}

	.depth1>li {
		display: table-cell;
		width: 180px;
	}

	.depth1>li:last-child {
		width: 70px;
	}

	.depth1>li>a {
		position: relative;
		font-size: 19px;
		color: #000;
		font-weight: 500;
		line-height: 80px;
	}

	.depth1-focus {
		color: #ed1c24 !important;
	}

	.depth2 {
		padding-top: 40px;
		padding-bottom: 20px;
	}

	.depth2 li a {
		display: inline-block;
		font-size: 15px;
		color: #fff;
		margin-bottom: 15px;
	}

	.lang {
		position: absolute;
		left: calc(100% + 100px);
		top: 26px;
		font-size: 0;
		white-space: nowrap;
	}

	.lang a {
		display: inline-block;
		font-size: 15px;
		color: #888;
		font-weight: 500;
		text-transform: uppercase;
	}

	.lang .lang-active {
		color: #000;
	}

	.lang-bar {
		display: inline-block;
		width: 1px;
		height: 9px;
		background-color: #999;
		margin: 0 10px;
	}
</style>