import axios from 'axios';

const state = {
  // dataÎ•º Î™®ÏïÑÏ£ºÎäî Í∞ùÏ≤¥Î•º ÎßåÎì§Ïñ¥Ïä¥
  gnbData: [],
  noticeData: [],
  newsData: []
};
const actions = {
  fetchGnbdata({commit}) {
    axios.get('./data/gnb.json')
      .then(response => {
        console.log('axios', response.data)
        commit('GNB_DATA', response.data)
      })
      .catch(err => console.log(err))
  },
  fetchNoticedata({commit}) {
    axios.get('./data/notice.json')
      .then(response => {
        console.log(response)
        commit('NOTICE_DATA', response.data)
      })
      .catch(err => console.log(err))
  },
  fetchNewsdata({commit}) {
    // news.json ÏùÑ axiosÎ°ú Ìò∏Ï∂úÌïúÎã§.
    axios.get('./data/news.json')
      .then(response => {
        console.log(response)
        // mutation ÏúºÎ°ú ÏûêÎ£å Ï†ÑÏÜ°, mutation ÏúºÎ°ú Ï†ÑÎã¨Ìï†Î†§Î©¥ commit ÌïÑÏöî
        // axiosÎäî Î∞õÏïÑÏò® ÏûêÎ£åÎ•º .dataÏóê Î≥¥Í≥º
        commit('NEWS_DATA', response.data)
      })
      .catch(err => console.log(err))
  },

};
const mutations = {
  GNB_DATA(state, payload) {
    console.log('gnbdata', payload)
    state.gnbData = payload
  },
  NOTICE_DATA(state, payload) {
    state.noticeData = payload
  },
  NEWS_DATA(state, payload) {
    state.newsData = payload
  }
};
const getters = {
  getGnbData(state) {
    return state.gnbData;
  },
  getNoticeData(state) {
    return state.noticeData;
  },
  getNewsData(state) {
    return state.newsData;
  }
};

export default {
  state,
  actions,
  mutations,
  getters
}